body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  display: flex;
  justify-content: center;
}

.Board {
  display: grid;
  grid-gap: 2px;
  gap: 2px;
}

.Tile {
  border: 1px solid lightgray;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

